import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { UserService } from '../../../../api/services/user.service';
import { FacilityService } from '../../../../api/services/facility.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as moment from 'moment';

import * as _ from 'underscore';

@Component({
  selector: 'app-facility-create-booking',
  templateUrl: './facility-create-booking.component.html',
  styleUrls: ['./facility-create-booking.component.scss']
})
export class FacilityCreateBookingComponent implements OnInit {


  isEditBooking:boolean = false;

  isBookingSubmitted:boolean = true;

  isError:boolean = false;
  alertMessage:string = "";

  booking:any;

  facilityCategoryData:any;
  facilitySlotData:any;
  statusTypeData:any;

  isSlotSelected:boolean = false;
  isBookingCancelled:boolean = false;
  isFacilitySelected:boolean = false;

  userIdData:any;

  cancelledUserId:any;

  bookingId:any;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
    private facilityService:FacilityService,
  	private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  getSlotTime(time){
    var date = moment(time, 'HH:mm:ss').toDate();
    return moment(date).format('HH:mm A');
  }

  isAdmin(){
    return ( this.cookieService.get('userRole') == 'Admin' || this.cookieService.get('userRole') == 'Staff') ? true : false;
  }

  getBookingStatus(){
    //cancelled status
    if(this.booking.facilityBookingStatusId == 190){
      this.isBookingCancelled = true;
      this.cancelledUserId = parseInt(this.cookieService.get('userId'));
    }
    else {
      this.isBookingCancelled = false;
      this.cancelledUserId = 1;
    }
  }

  getFacility(id) {

    var params = {
        apartmentId: parseInt(this.cookieService.get('apartmentId')),
        apartmentFacilityId: parseInt(this.booking.apartmentFacilityId)
    }

    this.facilityService.getApartmentFacilitySlotsByFacilityId(params).subscribe((res:any) => { 

      this.isFacilitySelected = true;

      this.facilitySlotData = res.filter(item => {
        return item.isActive;
      })

      console.log(this.facilitySlotData);

    },
    error => {

    });

  }

  emitSlotTime(){
    //this.isSlotSelected = true;
  }

  getSlot(id){
    console.log(id);
    this.isSlotSelected = true;

    this.facilityService.getApartmentFacilitySlotsById(parseInt(id)).subscribe((res:any) => {

      this.booking.amount = res[0].cost;

    },
    error => {

    });


  }

  submitFacilityBookingForm(form) {

    this.isBookingSubmitted = false;

    if(!this.isEditBooking) {

      let details = {

        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "apartmentFacilityId": parseInt(this.booking.apartmentFacilityId),
        "bookedOn": moment().format(), // today's date
        "bookedForDate": this.booking.bookedForDate,
        "slotId": parseInt(this.booking.slotId),
        "facilityBookingStatusId": parseInt(this.booking.facilityBookingStatusId),
        "isCancelled": this.isBookingCancelled,
        "cancelledBy": this.cancelledUserId,
        "cancelledOn": "",
        "cancelledAmount": 0,
        "neededUserId": parseInt(this.booking.neededUserId),
        "amount": this.booking.amount,
        "comment": this.booking.comment,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": moment().format(),
        "updatedBy": null,
        "updatedOn": null

      }

      this.facilityService.addApartmentFacilityBooking(details).subscribe((res:any) => {

        if(res.message){

          this.isBookingSubmitted = true;
          this.sharedService.setAlertMessage("Booking done successfully");
          if(this.isAdmin())
            this.router.navigate(['ams/facility/booking-actions']);
          else
            this.router.navigate(['user/facility-booking/all-bookings']);

        }
        else {
          this.isBookingSubmitted = true;
          this.isError = true;
          this.alertMessage = res.errorMessage;
        }

      },
      error => {

        this.isBookingSubmitted = true;
        this.isError = true;
        this.alertMessage = "Some error occured";

      });

    }

    else {

        let details = {
          "apartmentFacilityBookingId": this.booking.apartmentFacilityBookingId,
          "apartmentId": parseInt(this.cookieService.get('apartmentId')),
          "apartmentFacilityId": parseInt(this.booking.apartmentFacilityId),
          "bookedOn": moment().format(), // today's date
          "bookedForDate": this.booking.bookedForDate,
          "slotId": parseInt(this.booking.slotId),
          "facilityBookingStatusId": parseInt(this.booking.facilityBookingStatusId),
          "isCancelled": this.isBookingCancelled,
          "cancelledBy": this.cancelledUserId,
          "cancelledOn": "",
          "cancelledAmount": 0,
          "neededUserId": parseInt(this.booking.neededUserId),
          "amount": this.booking.amount,
          "comment": this.booking.comment,
          "isActive": true,
          "insertedBy": 0,
          "insertedOn": moment().format(),
          "updatedBy": parseInt(this.cookieService.get('userId')),
          "updatedOn": null

      }


      this.facilityService.updateApartmentFacilityBooking(details).subscribe((res:any) => {

        if(res.message){

          this.isBookingSubmitted = true;
          this.sharedService.setAlertMessage("Booking updated successfully");
          if(this.isAdmin())
            this.router.navigate(['ams/facility/booking-actions']);
          else
            this.router.navigate(['user/facility-booking/all-bookings']);

        }
        else {
          this.isBookingSubmitted = true;
          this.isError = true;
          this.alertMessage = res.errorMessage;
        }

      },
      error => {

        this.isBookingSubmitted = true;
        this.isError = true;
        this.alertMessage = "Some error occured";

      });

    }


    



  }

  ngOnInit() {

  	this.booking = {};

    this.booking.slotId = "";
    this.booking.apartmentFacilityId = "";
    this.booking.facilityBookingStatusId = "";
    this.booking.neededUserId = "";
    this.booking.comment = "";


    if(this.route.params['value'].id != undefined) {

      this.isEditBooking = true;

      this.bookingId = this.route.params['value'].id;
      
      this.facilityService.getApartmentFacilityBookingsByFacilityBookingId(this.bookingId).subscribe((res:any) => {
          
        this.booking = res[0];
        this.getFacility(this.booking.apartmentFacilityId);

        console.log(res);
      
      },
      error => {
        
      }
      );

    }

  	this.facilityService.getApartmentFacilitiesByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

      this.facilityCategoryData = res.filter(item => {
        return item.isActive;
      })

    },
    error => {

    });

    

    //status 
    this.lookupService.getLookupValueByLookupTypeId(40).subscribe((res:any) => {
          
      this.statusTypeData = res.filter( item => {
          return item.isActive;
      });

    },
    error => {
      
    }
    );


    //get all users
    this.userService.getAllUsersByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {
       
      this.userIdData = res;

      console.log(this.userIdData);

      if(this.cookieService.get('userRole') == 'Tenant' || this.cookieService.get('userRole') == 'Owner') {

        this.userIdData = res.filter(item => {
          return item.userId == parseInt(this.cookieService.get('userId'));
       });

       this.booking.neededUserId = this.userIdData[0].userId;

       this.booking.facilityBookingStatusId = 189 /* default pending status */

      }

    },
    error => {
      
    }
    );


  }

}
